<template>
  <div class="modal-outer">
    <div class="modal-backdrop"></div>
    <div class="modal">
      <div class="modal-content">
        <div v-if="mode === 'learn' && name === 'full'">
          <div class="status learn">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations!
          </div>
          <div class="learnt">
            You have learnt all countries. You are now ready for exciting game!
          </div>
        </div>
        <div v-else-if="mode === 'learn' && name !== 'full'">
          <div class="status learn">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations!
          </div>
          <div class="learnt">
            You have learnt all {{ fullName }} countries.
          </div>
        </div>
        <div v-else>
          <div v-if="reason === 'lose'" class="status lose">
            <img src="../assets/images/cross.svg" height="24" width="24" />
            You lose
          </div>
          <div v-else class="status win">
            <img src="../assets/images/correct-symbol.svg" height="24" width="24" />
            Congratulations! You win!
          </div>
          <div class="score">
            Your score: 
            <span :class="score >= best_score ? 'green' : 'tomato'">{{ score }}</span>
          </div>
          <div class="best-score">
            Your best score: {{ best_score }}
          </div>
        </div>
        <router-link to="/" tag="button" class="btn">Home</router-link>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'

export default Vue.component('modal', {
  data () {
    return {
      best_score: 0
    }
  },
  created () {
    if (this.mode === 'game') {
      this.getBestScore()
    }
  },
  props: {
    mode: {
      type: String,
      required: false
    },
    reason: {
      type: String,
      required: true
    },
    score: {
      type: Number,
      default: 0
    }
  },
  computed: {
    name () {
      return this.$route.params.name
    },
    fullName () {
      switch (this.name) {
        case 'europe':
          return 'European'
        case 'asia':
          return 'Asian'
        case 'africa':
          return 'African'
        case 'na':
          return 'North American'
        case 'sa':
          return 'South American'
        case 'oceania':
          return 'Oceanian'
      }
    }
  },
  methods: {
    getBestScore () {
      if (localStorage.getItem('best_score')) {
        if (this.score > +localStorage.getItem('best_score')) {
          this.best_score = this.score
          localStorage.setItem('best_score', this.best_score)
        } else {
          this.best_score = +localStorage.getItem('best_score')
        }
      } else {
        this.best_score = this.score
        localStorage.setItem('best_score', this.best_score)
      }
    }
  }
})
</script>
<style scoped>
body {
  overflow: hidden;
}
.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  outline: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  opacity: 0.5;
  background-color: #000;
}
.modal-content {
  animation: tiltup 0.7s;
  width: 500px;
  transform: perspective(300px);
  padding-bottom: 30px;
  background: #fff;
  font-size: 24px;
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
}
.status {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid #e5e5e5;
}
.modal-content::before {
  background: linear-gradient(to right, #9BFFE6 0%, #A3CCFF 100%);
  content: "";
  display: block;
  height: 3px;
  width: 100%;
}
.status img {
  margin-right: 10px;
}
.lose {
  color: tomato;
}
.win {
  color: #3a87ad;
}
.green {
  color: #91DC5A;
}
.tomato {
  color: tomato;
}
.score, .best-score {
  margin-top: 10px;
}
.best-score {
  margin-bottom: 20px;
}
.learnt {
  padding: 20px;
}
img {
  animation: img_scale 1s infinite;
}
@keyframes tiltup {
  0% { 
    bottom: -400px;
    opacity: 0; 
    transform: rotateX(-90deg);
    border-top: solid 40px; 
  } 
  30% {
    opacity: 0;
  }
  100% {
    bottom: 0;
  }
}
@keyframes img_scale {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
</style>


