<template>
  <div>
    <Loader v-show="showLoader" /> 
    <Map v-if="loaded" :mode="'game'" :geojson="geojson" :mapOptions="options" :stopLoader="stopLoader" />
  </div>
</template>
<script>
import Map from '../components/Map'
import Loader from '../components/Loader'

export default {
  data () {
    return {
      geojson: [],
      options: {
        zoom: 2,
        center: [40, 0],
        maxBounds: [[90, -180], [-60, 225]]
      },
      loaded: false,
      showLoader: true
    }
  },
  created () {
    this.getContinent()
  },
  methods: {
    getContinent () {
      let xhr = new XMLHttpRequest()
      xhr.open('GET', `../json/full.geo.json`, true)
      xhr.onload = () => {
        if (xhr.status === 200) {
          this.geojson = [...JSON.parse(xhr.responseText).features]
        }
        this.loaded = true
      }
      xhr.send()
    },
    stopLoader () {
      this.showLoader = false
    }
  },
  components: {
    Map,
    Loader
  }
}
</script>


