import Vue from 'vue'
import Router from 'vue-router'
import Start from '../pages/Start'
import Game from '../pages/Game'
import Learn from '../pages/Learn'
import Continent from '../components/Continent'

Vue.use(Router)
export default new Router({
  mode: 'history',
  base: '/',
  linkActiveClass: 'active',
  routes: [
    {
      name: 'Start',
      path: '/',
      component: Start   
    },  
    {
      name: 'Game',
      path: '/game',
      component: Game,
    },
    {
      name: 'Learn',
      path: '/learn',
      component: Learn,
      redirect: '/learn/full',
      children: [
        {
          path: '/learn/:name',
          component: Continent,
          name: 'LearnContinent',
          props: { mode: 'learn' }
        }
      ]
    }
  ]
})


