<template>
  <div id="map">
    <Modal v-if="game.attempts === 0 && mode === 'game'" :mode="mode" :reason="'lose'" :score="game.score"/>
    <Modal v-if="game.count === countries.length" :mode="mode" :reason="'win'" :score="game.score" />
    <Capital v-if="right && mode === 'game'" :country="countries[game.count].name" :capitals="capitals" :rightCapital="countries[game.count].capital" :check="checkCapital" />
    <div class="notification" :class="animation ? 'animation' : ''" v-if="mode === 'game'" @animationend="endAnime" @webkitAnimationEnd="endAnime" @msAnimationEnd="endAnime" @mozAnimationEnd="endAnime">
      <div class="notification_img">
        <img src="../assets/images/information.svg" height="48" width="48">
      </div>
      <div class="notification_info">
        <div class="country" v-if="game.count !== countries.length">
          {{ countries[game.count].name }}
        </div>
        <div class="digits">
          Score: {{ game.score }}
          <span class="attempts">Attempts: </span>
          <span :class="attempts_animation ? 'attempts_animation' : ''" @animationend="endAttemptsAnime" @webkitAnimationEnd="endAttemptsAnime" @msAnimationEnd="endAttemptsAnime" @mozAnimationEnd="endAttemptsAnime">{{ game.attempts }}</span>
        </div>
      </div>
    </div>
    <div class="learn_notification" :class="animation ? 'animation' : ''" v-else-if="mode === 'learn' && game.count !== countries.length" @animationend="endAnime" @webkitAnimationEnd="endAnime" @msAnimationEnd="endAnime" @mozAnimationEnd="endAnime">
      {{ countries[game.count].name }}
    </div>
    <v-map ref="map" :minZoom="2" :options="mapOptions" class="fullHeight">
      <v-geojson :geojson="geojson" :options="options.geojson"></v-geojson>
    </v-map>
  </div>
</template>
<script>
import Vue from 'vue'
import Vue2Leaflet from 'vue2-leaflet'
import Modal from '../modals/Modal'
import Capital from '../modals/Capital'

export default Vue.component('Map', {
  data () {
    return {      
      options: {
        geojson: {
          stroke: true,
          fill: true,
          strokeColor: '#000',
          color: '#000',
          fillColor: '#fff',
          fillOpacity: 1,
          onEachFeature: this.onEachFeature
        }
      },
      countries: [],
      game: {
        count: 0,
        attempts: 5,
        score: 0
      },
      layers: [],
      animation: false,
      attempts_animation: false,
      right: false,
      capitals: []
    }
  },
  created () {
    for (let i = 0; i < this.geojson.length; i++) {
      this.countries.push({
        name: this.geojson[i].properties.name,
        capital: this.geojson[i].properties.capital
      })
    }
    this.countries.sort(this.compareRandom)
  },
  mounted () {
    this.stopLoader()
  },
  props: {
    mode: {
      type: String,
      required: true
    },
    geojson: {
      required: true
    },
    mapOptions: {
      type: Object,
      required: true
    },
    stopLoader: {
      type: Function
    }
  },
  methods: {
    onEachFeature (feature, layer) {
      layer.bindPopup(this.popup(layer))      
      this.layers.push(layer)
      layer.on('click', () => {
        this.show(layer)
      }, this)
    },
    show (layer) {
      this.capitals = []
      if (layer.feature.properties.name === this.countries[this.game.count].name) {        
        while (this.capitals.length < 3) {
          let randomnumber = Math.floor(Math.random() * this.countries.length)
          if (this.capitals.indexOf(randomnumber) > -1) {
            continue
          }
          if (this.countries[randomnumber].capital !== this.countries[this.game.count].capital) {
            this.capitals.push({name: this.countries[randomnumber].capital, isAnswered: false})
          } else {
            continue
          }
        }
        this.capitals.push({name: this.countries[this.game.count].capital, isAnswered: false})        
        this.capitals.sort(this.compareRandom)
        if (this.mode === 'game') {
          this.right = true
        } else {
          this.animation = true
          this.game.count++
          this.game.attempts = 5
        }
        this.game.score++
        layer.setStyle({fillColor: this.randomColor()})
        layer.off('click')
      } else {
        this.attempts_animation = true
        this.game.attempts--
        if (this.mode === 'learn' && this.game.attempts === 0) {
          for (let i = 0; i < this.layers.length; i++) {
            if (this.layers[i].feature.properties.name === this.countries[this.game.count].name) {
              this.layers[i].setStyle({fillColor: '#9BFFE6'})
              this.$refs.map.mapObject.flyTo({
                lat: ((this.layers[i]._bounds._northEast.lat + this.layers[i]._bounds._southWest.lat) / 2),
                lng: ((this.layers[i]._bounds._northEast.lng + this.layers[i]._bounds._southWest.lng) /2)
              }, 6)
              break
            }
          }
        }
      }
    },
    checkCapital (answered) {
      if (answered) {
        this.game.score++
      }    
      this.right = false
      this.animation = true
      this.game.count++
      this.game.attempts = 5
    },
    compareRandom () {
      return Math.random() - 0.5
    },
    randomColor () {
      return `rgb(${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)},${Math.floor(Math.random() * 255)})`
    },
    popup (layer) {
      return layer.feature.properties.name
    },
    endAnime () {
      this.animation = false
    },
    endAttemptsAnime () {
      this.attempts_animation = false
    }
  },
  components: {
    'v-map': Vue2Leaflet.Map,
    'v-geojson': Vue2Leaflet.GeoJSON,
    Modal,
    Capital
  },
})
</script>
<style scoped>
@import "~leaflet/dist/leaflet.css";

.top_bar {
  width: 1000px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.notification, .learn_notification {
  position: absolute;
  right: 50px;
  top: 50px;
  width: 300px;
  min-height: 100px;
  display: flex;
  align-items: center;
  color: #404040;
  background-color: #9BFFE6;
  border-bottom: 5px solid rgba(0,0,0,0.9);
  box-shadow: 0px 0px 11px rgba(0, 0, 0, 0.3);
  z-index: 1050;
}
.learn_notification {
  min-height: 50px;
  justify-content: center;
  font-size: 20px;
}
.notification_img {
  min-height: 100px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  background-color: #4d7f73;
}
.notification_info {
  width: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
}
.attempts {
  margin-left: 20px;
}
@media screen and (max-width: 400px) {
  .notification, .learn_notification {
    right: 0;
    top: 0;
    width: 100%;
  }
}
.animation {
  animation-name: next;
  animation-duration: 0.5s;
  animation-timing-function: ease-in-out;
}
.attempts_animation {
  animation: attempts 1s ease;
}
.country {
  padding: 10px 10px 20px 10px;
  font-size: 20px;
}
.fullHeight {
  min-height: 100vh;
}
.leaflet-container {
  background-color: #A3CCFF;
}
@keyframes next {
  50% {
    transform: translateX(400px);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}
@keyframes attempts {
  25% {
    color: tomato;
  }
  50% {
    color: #fff;
  }
  75% {
    color: tomato;
  }
  100% {
    color: #fff;
  }
}
</style>


