<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app'
}
</script>

<style>
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.btn {
  position: relative;
  width: 200px;
  height: 50px;
  line-height: 1;
  background: #9BFFE6;
  color: #2c3e50;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 5px 0px 0px #6cb2a1;
  box-shadow: 0px 5px 0px 0px var(--box-shadow-color, #6cb2a1);
  font-size: 16px;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 2px;
  cursor: pointer;
  outline: none;
  overflow: hidden;
  transition: all 0.2s ease;
}
.btn:hover {
  background: #7ed3bf;
}
.btn:active {
  transform: translate(0px, 5px);
  -webkit-transform: translate(0px, 5px);
  box-shadow: 0px 0px 0px 0px;
}
</style>



